
article {
    background: #fff;
    width: 80%;
    text-align: center;
    min-height: 100%;
    margin: 0 auto;
    margin-bottom: .2rem;
    padding: .2rem .4rem;
    box-shadow: 1px 1px 2px rgba(0,0,0,.1), 0 1px 1px rgba(0,0,0,.1);
    border-radius: 3px;
    max-width: 1200px;
    font-size: .16rem;
}
article a {
    text-decoration: none;
    color: #333;
}




